body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: #0f0c29;
  background: -webkit-linear-gradient(to top, #24243e, #302b63, #0f0c29);
  background: -webkit-gradient(linear, left bottom, left top, from(#24243e), color-stop(#302b63), to(#0f0c29));
  background: -webkit-linear-gradient(bottom, #24243e, #302b63, #0f0c29);
  background: linear-gradient(to top, #24243e, #302b63, #0f0c29);
}

* {
  box-sizing: border-box;
}

/*  HEADER SECTION*/
.header {
  width: 100%;
  background: #0f0c29;
  margin-bottom: 2rem;
  height: 70px;
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 999;
}

.content {
  margin: 0 auto;
  width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list li {
  display: inline-block;
  color: #e0e0e0;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: lighter;
  margin-right: 20px;
  text-decoration: none;
}

.list li a {
  color: #e0e0e0;
  text-decoration: none;
}

.link {
  text-decoration: none;
  color: #e0e0e0;
}

.link:hover {
  color: #949494;
}

.searcher {
  width: 15px;
  cursor: pointer;
}

.fa-search {
  font-size: 18px;
  cursor: pointer;
}

.fa-search:hover {
  color: #949494;
}

.list li:nth-child(n + 2) {
  margin-left: 25px;
}

.logo {
  display: flex;
  margin-left: 20px;
  padding-top: 10px;
}

.logo img {
  width: 40px;
  height: 40px;
  position: relative;
}

.logo h3 {
  margin-left: 10px;
  font-size: 25px;
  margin-bottom: 0px;
  position: relative;
  bottom: 15px;
  color: #e0e0e0;
}

/*  NEW RELEASES SECTION*/
.newReleases {
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  border-bottom: 1px solid #505089;
}

.poster img {
  width: 200px;
  border-radius: 5px;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
}

.overlay:hover img {
  background-color: #000;
  opacity: 0.5;
}

.movie-container {
  max-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin-bottom: 10px;
}

.vote {
  z-index: 1;
  margin-bottom: 10px;
}

.newReleases h2 {
  color: #e0e0e0;
}

.movie-carousel {
  display: flex;
  max-width: 1200px;
  justify-content: space-between;
}

.new-releases-slide {
  display: flex;
  justify-content: space-around;
  min-width: 1200px;
  position: relative;
}

.title h3 {
  text-align: left;
  width: 200px;
  padding-top: 20px;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}

.release-date {
  width: 200px;
  line-height: 0;
  text-align: left;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.banner-next-arrow {
  color: #fff;
  position: relative;
  bottom: 17rem;
  float: right;
}

.banner-back-arrow {
  color: #fff;
  position: relative;
  bottom: 15rem;
  width: 0;
}



/* NOW PLAYING SECTION */
.now-playing-slide {
  display: flex;
  justify-content: space-around;
  min-width: 1200px;
  position: relative;
}

.now-playing {
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 3rem;
  overflow: hidden;
  border-bottom: 1px solid #505089;
}

.now-playing h2 {
  color: #e0e0e0;
}

.now-playing-carousel {
  display: flex;
  max-width: 1200px;
  justify-content: space-between;
}

/*  TRENDING SECTION */
.slider {
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  display: flex;
  margin-top: 100px;
}

.slider-wrapper {
  display: flex;
  position: relative;
  height: 100%;
  width: 100%;
}

.nextArrow {
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
  color: #fff;
  cursor: pointer;
}

.backArrow {
  position: absolute;
  top: 50%;
  left: 25px;
  z-index: 1;
  color: #fff;
  cursor: pointer;
  width: 0;
}

.vote {
  width: 30px;
  background-color: #aaa;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  position: relative;
  top: 3rem;
  right: 5px;
  font-size: 15px;
}

.inf {
  position: relative;
  bottom: 13rem;
  left: 2rem;
  color: #fff;
}

.inf h3 {
  font-weight: 400;
  font-size: 1.2rem;
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}

.title h2 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2rem;
  margin-top: 6px;
  margin-bottom: 8px;
}

.rating {
  display: flex;
  align-items: center;
}

.rating img {
  width: 25px;
  height: 25px;
  position: relative;
  bottom: 4px;
  margin-right: 10px;
}

.genre {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2rem;
  margin-right: 5px;
}

/*  TRAILER SECTION  */

.youtube {
  width: 800px;
  height: 450px;
}

.trailers {
  margin: 0 auto;
  margin-top: 3rem;
  max-width: 1200px;
  border-bottom: 1px solid #505089;
}

.trailer-carousel {
  display: flex;
}

.trailers h1 {
  max-width: 1200px;
  color: #e0e0e0;
}

.youtube-videos {
  max-width: 800px;
  margin: 0 auto;
  overflow: hidden;
}

/* TOP RATED */
.top-rated-slide {
  display: flex;
  justify-content: space-around;
  min-width: 1200px;
  position: relative;
}

.top-rated {
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 3rem;
  overflow: hidden;
}

.top-rated h2 {
  color: #e0e0e0;
}

.top-rated-carousel {
  display: flex;
  max-width: 1200px;
  justify-content: space-between;
}

/* SEARCH BOX */
.search-box {
  position: relative;
}

.input-box {
  position: absolute;
  right: 0;
  z-index: 1;
  top: 3.5rem;
  display: flex;
  flex-direction: row;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #242628;
}

.input-box input {
  flex-grow: 1;
  color: #bfd2ff;
  font-size: 1rem;
  line-height: 2.4rem;
  vertical-align: middle;
  border-style: none;
  background: transparent;
  outline: none;
  padding-left: 10px;
}

.input-box button {
  color: #7881a1;
  font-size: 1rem;
  line-height: 2.4rem;
  vertical-align: middle;
  -webkit-transition: color 0.25s;
  transition: color 0.25s;

  background: none;
  border: none;
  outline: none;
}

.input-box button:hover {
  color: #bfd2ff;
}

.notShow {
  display: none;
}

